<template>
  <div class="dialogue-info">
    <header id="wx-header">
      <!--<div class="other"><span>添加朋友</span></div>-->
      <div class="center">
        <div class="iconfont icon-return-arrow" v-on:click="$router.back()">
          <span>返回</span>
        </div>
        <span>选择联系人</span>
        <span style="position: absolute;right:15px;top:0;">
          <van-button type="primary" size="small" @click="confirm">确定 ({{checkFriendList.length}})</van-button>
        </span>
      </div>
    </header>
    <van-row class="friendList">
      <div v-show="isShow" style="color:#999;margin:10px 0 20px 5px">选择联系人......</div>
      <van-col span="3" v-for="(item,index) in checkFriendList" :key="index">
        <div>
          <img :src="'/api/file/showImg?fileUrl='+item.user.headerUrl" alt class="imgStyle" />
        </div>
      </van-col>
    </van-row>

    <!-- <van-checkbox-group v-model="result" > -->
    <van-cell-group>
      <van-cell v-for="(item, index) in newList" clickable :key="index">
        <img :src="'/api/file/showImg?fileUrl='+item.user.headerUrl" alt class="imgStyle" />
        <span>{{item.user.nickname}}</span>
        <van-checkbox
          :name="item.user.userId"
          ref="checkboxes"
          slot="right-icon"
          :disabled="item.disabled"
          @change="change"
          @click="toggle(index)"
          v-model="item.checked"
        />
      </van-cell>
    </van-cell-group>
    <!-- </van-checkbox-group> -->
  </div>
</template>
<script>
import { getMessage, send } from "../../socket/socket";
import qs from "qs";
export default {
  data() {
    return {
      isShow:true,
      result: [],
      friendList: [],
      newList: [],
      userInfo: "",
      groupInfo: [],
      checkFriendList: []
    };
  },
  created() {
    this.userInfo = JSON.parse(window.localStorage.getItem("userInfo"));
    this.getFriendList();
    this.findGroupInfo();
  },

  methods: {
    //好友列表
    getFriendList() {
      this.$http.get("/api/friend/getFriendList").then(res => {
        if (res.data.code == 200) {
          this.friendList = res.data.data;
          this.getArrEqual(this.friendList, this.groupInfo);
          this.$nextTick(() => {
            this.newList = this.friendList;
          });
          // console.log(this.newList);
        }
      });
    },
    //群里好友信息
    findGroupInfo() {
      let params = {
        roomId: this.$route.query.roomId
      };
      this.$http
        .post("/api/chat/findGroupInfo", qs.stringify(params))
        .then(res => {
          if (res.data.code == 200) {
            this.groupInfo = res.data.data.groupMemberList;
            this.getFriendList();
            this.$nextTick(() => {
              // this.getArrEqual(this.friendList, this.groupInfo);
            });
            // console.log(this.newList);
          }
        });
    },
    //查找相同项
    getArrEqual(arr1, arr2) {
      let newArr = [];
      for (let i = 0; i < arr2.length; i++) {
        for (let j = 0; j < arr1.length; j++) {
          // arr1[j].disabled = false;

          if (arr1[j].user.userId === arr2[i].userId) {
            arr1[j].disabled = true;
            arr1[j].checked = true;
            newArr.push(arr1[j]);
          }
        }
      }
      return newArr;
    },
    toggle(index) {
      // this.$refs.checkboxes[index].toggle();
      // console.log(index);
    },
    change(data) {
      // console.log(data);
      
      this.checkFriendList = [];
      this.newList.map(res => {
        if (!res.disabled && res.checked) {
          // console.log(res);
          this.checkFriendList.push(res);
        }
      });
      // console.log(this.checkFriendList);
      if(this.checkFriendList.length>0){
        this.isShow=false;
      }else{
        this.isShow=true;
      }
    },
    //确认拉人
    confirm() {
      let result = [];
      this.checkFriendList.map(item => {
        result.push(item.user.userId);
      });
      let params = {
        dataGroup: "1",
        dataType: "5",
        roomId: this.$route.query.roomId,
        from: this.userInfo.userId,
        msgType: "text",
        pullers: result.join(",")
      };
      send(params);
      this.$router.back();
    }
  },
  mounted() {}
};
</script>
<style>
.dialogue-info {
  background: white;
}
.imgStyle {
  width: 40px;
  height: 40px;
  border-radius: 5px;
}
.friendList {
  background: white;
  padding: 10px;
  height: 66px;
}
</style>


